.dashboardsection {
  display: grid;
  grid-template-areas: "a a";
  grid-gap: 40px;
  margin-top: 2%;
  margin-left: 4%;
  margin-right: 4%;

  align-items: flex-start;
}

.tables {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 20px;
  color: #424242;
}

.tablecol {
  display: flex;
  flex-direction: row;
  margin: 5% 0 0 0;

}

.heading {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 700;
  font-size: 16px;
  color: #424242;
  margin: 5% 0 2% 0;
}

.tablecolumn1 {
  width: 70%;
  margin: 5% 2% 0 4%;
}

.dashboardtablecontainer {
  background-color: #fff;
}

.tablegrid {
  display: grid;
  justify-items: center;
  grid-template-columns: repeat(7, 1fr);
}

.tablenumber {
  margin: 10% 5% 0% 5%;
  width: 57px;
  height: 57px;
  border-radius: 5px;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 700;
  font-size: 30px;
  color: #7a79a0;
  justify-content: center;
  align-items: center;
  display: flex;
}

.tablecolumn2 {
  width: 170px;
  margin: 5% 0 0 0 ;
}

.tablerow {
  flex-direction: row;
  display: flex;
  width: 150px;
  height: 85px;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  margin: 2% auto 0 auto;
}

.number {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 700;
  font-size: 50px;
  color: #ff7377;
  margin-right: 6%;
}

.number2 {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 700;
  font-size: 50px;
  color: #1cab33;
  margin-right: 6%;
}
.text {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 300;
  font-size: 14px;
  color: black;
  display: grid;
}

.inventory {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 20px;
  color: #424242;
  margin-top: 2%;
}

.dashboardinventorycol {
  display: flex;
  flex-direction: row;
}

.invcolumn1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 170px;
  height: 177px;
  margin: 5% 2% 10% 8%;
  background-color: #fff;
}

.invcolumn2 {
  display: flex;
  flex-direction: column;
  margin: 5% 0% 0% 0%;
}
.buttoncontainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.button {
  background-color: transparent;
  border: 1px solid #1dab33;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 300;
  font-size: 14px;
  color: #1dab33;
  border-radius: 5px;
  padding: 10px 30px;
}
.headerrow {
  display: grid;
  grid-template-columns: 0.5fr 2fr 1fr;
  margin-bottom: 2%;
}

.text1 {
  width: 8%;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;

}
.text2 {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 12px;
}
.text3 {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 12px;
}

.list{
  width: 80%;
  height: 155px;
  background-color: #fff;
  overflow-y: scroll;
}

.list::-webkit-scrollbar{
  display: none;
}

.inventorylistgrid{
  display: grid;
  grid-template-columns: 0.5fr 2fr 1fr;
  margin-top: 2%;

}

.inventoryindex{
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 12px;
  color: #555570;
  display: flex;
  justify-content: center;
  align-items: center;
}

.inventoryname{
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 12px;
  color: #555570;
}

.inventorystock{
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 12px;
  color: #555570;
}
.orderscreen {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 20px;
  color: #424242;
}
.outer {
}
.dashboardouterborder {
  border-color: #df3030;
  border-style: solid;
  border-width: 3px;
  border-radius: 30px;
  width: 100%;
  height: 343px;
  margin-top: 2%;
  overflow-y: scroll;
  -ms-overflow-style: none;
  scrollbar-width: 0;
}

.dashboardouterborder::-webkit-scrollbar {
  display: none;
}

.ordergrid {
  display: grid;
  grid-template-areas: "a a";
  justify-content: center;
  align-items: center;
  grid-gap: 20px;
  margin: 4% 0 4% 0;
}

.orderdetails {
  background-color: #fff;
  border-style: solid;
  border-color: #cacaca;
  border-width: 0.5px;
  border-radius: 10px;
  width: 252px;
  height: 141px;
}

.orderID {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 800;
  font-size: 16px;
  line-height: 22px;
  color: #424242;
  margin: 6% 0 2% 8%;
}

.orderdetail {
  display: flex;
  flex-direction: row;
  margin: 0 0 5% 8%;
}

.time {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 14px;
  color: #979797;
}

.tableID {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 14px;
  color: #df3030;
}

.orderbutton {
  background-color: transparent;
  border: 1px solid #1dab33;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 16px;
  color: #1dab33;
  border-radius: 5px;
  padding: 15px 30px;
  width: 210px;
}

.promo {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 600;
  font-size: 20px;
  color: #424242;
}

.dashboardpromocontainer {
  margin-top: 2%;
  display: flex;
  flex-direction: column;
  height: auto;
}

.promodetails {
  width: 520px;
  height: 153px;
  margin-bottom: 2%;
}

.picture {
  width: 100%;
  height: 100%;
}
